section {
  scroll-margin-top: 100px;
}

span {
  color: #38bdf8;
}

/** Media Queries **/
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.contacto {
  text-align: center;
  padding: 2rem;
  margin-top: 2rem;
  background: linear-gradient(135deg, #05097b 0%, #04124e 50%, #06086e 100%);
}
@media (min-width: 769px) {
  .contacto h1 {
    font-size: 3rem;
  }
  .contacto .contactos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    padding: 1rem;
  }
}

.contactos {
  margin: 1rem;
}

.contacto__links {
  padding: 1rem;
  background: linear-gradient(135deg, #06196e 0%, #0c1b3f 50%, #041975 100%);
  margin: 1rem 0;
}
@media (min-width: 769px) {
  .contacto__links {
    margin: 0;
  }
}
.contacto__links .linked {
  padding: 1rem;
  text-align: left;
  display: flex;
  align-items: left;
  flex-direction: row;
  text-decoration: none;
  font-size: 0.5rem;
  margin: 6rem 2rem;
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 400px) {
  .contacto__links .linked {
    flex-direction: row;
    padding: 3rem;
    align-items: center;
  }
}
@media (min-width: 400px) {
  .contacto__links .linked {
    font-size: 1rem;
  }
}
.contacto__links .linked:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.contacto__links .linked p {
  margin: 0.5rem 0;
}
.contacto__links .linked i {
  padding: 1rem 0;
  font-size: 1rem;
}
@media (min-width: 400px) {
  .contacto__links .linked i {
    padding: 0;
    font-size: 4rem;
  }
}
.contacto__links .linked .text_link {
  margin: 0 1rem;
  color: white;
  text-align: left;
}

.contacto__form {
  padding: 1rem;
  background: linear-gradient(135deg, #06196e 0%, #0c1b3f 50%, #041975 100%);
}
.contacto__form .form {
  padding: 1rem;
}
.contacto__form .form input:not([type=submit]),
.contacto__form .form textarea,
.contacto__form .form select {
  padding: 0.5rem;
  display: block;
  width: 100%;
  background-color: white;
  border: 1px solid rgba(255, 255, 255, 0.5176470588);
  border-radius: 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.contacto__form textarea {
  height: 20rem;
}
.contacto__form button {
  background-color: rgba(0, 28, 88, 0.9450980392);
  color: white;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 5/2;
  display: block;
  border: none;
  border-radius: 10%;
  color: #38bdf8;
}
.contacto__form button:hover {
  background-color: rgba(0, 19.8863636364, 62.5, 0.9450980392);
  cursor: pointer;
}

.contacto__info label {
  margin: 1rem 0;
}